.camp{
    margin: 24px;
    font-family: 'Montserrat', sans-serif;
}

.camp label{
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: 600;   
    display: block ;   
    margin-bottom: 8px; 
}

.camp input{
    width: 100%;
    background-color: #FFFFFF;
    box-shadow: 10px 10px 30px 0px rgba(0, 0, 0, 0.06); 
    border:none;
    padding: 30px 20px;
    outline-color: #6278F7;
    font-size: 16px;
    box-sizing: border-box;
}


.camp button{
    border-radius: 10px;
    background: #6278F7;
}

.camp-color input{
    height: 40px;
    padding: 5px 25px;
}