.lista-opciones{
    margin: 24px;
}

.lista-opciones label{
    display: block;
    font-size: 18px;
    font-weight: 600;    
    margin-bottom: 8px; 
    font-family: 'Montserrat',sans-serif;
}

.lista-opciones select{
    width: 100%;
    background-color: #FFFFFF;
    box-shadow: 10px 10px 30px 0px rgba(0, 0, 0, 0.06); 
    border:none;
    padding: 30px 20px;
    outline-color: #6278F7;
    font-size: 16px;
    
}