.form{
    display: flex;
    justify-content: space-evenly;
    margin: 80px 50px;
    flex-wrap: wrap;
    gap: 20px;
}

.form form{
    
    background-color: #F6F6F6;
    box-shadow: 7px 7px 15px 0px rgba(0, 0, 0, 0.318); 
    border-radius: 20px;  
    padding: 8px 100px ;
    font-family: 'Prata', serif;
    flex: 1;
}

.form h2{
    font-size: 32px;
    font-weight: 400;
    color: #212121;
}